import React from "react";
import { Box, Typography } from "@mui/material";

export default function Logo(): JSX.Element {
  const height = "8vh";
  return (
    <Box
      sx={{ display: "flex", justifyContent: "center", alignItems: "center", height, columnGap: 1 }}
    >
      <Box sx={{ height }}>
        <img
          className="football-icon"
          alt="logo"
          src={`${process.env.PUBLIC_URL}/assets/images/football.png`}
          height="100%"
          width="auto"
        />
      </Box>
      <Typography
        lineHeight={height}
        component="h1"
        fontSize={`calc(${height} - 10px)`}
        textAlign="center"
        color="#16B7EA"
        fontFamily="Grandstander"
      >
        FPL Zone
      </Typography>
    </Box>
  );
}
