import { Box, Container, Icon, Link, Typography } from "@mui/material";
import Logo from "./logo";
import GitHubIcon from "@mui/icons-material/GitHub";
import { YouTube, Article, Slideshow } from "@mui/icons-material";
import CarouselRender from "./carousel";
import ImageModal from "./image_modal";
import { useState } from "react";

const linkStyling = {
  textDecoration: "none",
  "& :hover": {
    ".MuiTypography-root": {
      color: "#16B7EA",
    },
  },
};

const renderLink = (name: string, href: string, icon: JSX.Element): JSX.Element => {
  return (
    <Link href={href} target="_blank" sx={linkStyling}>
      <Box
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          columnGap: 1,
        }}
      >
        {icon}
        <Typography>{name}</Typography>
      </Box>
    </Link>
  );
};

export default function Home(): JSX.Element {
  const [selectedImage, setSelectedImage] = useState<string | null>(null);
  const [modalOpen, setModalOpen] = useState<boolean>(false);
  const images = ["gameweek-live", "my-team", "fix-and-res", "analysis", "login", "register"];

  const onImageClick = (imageIndex: number): void => {
    setSelectedImage(images[imageIndex]);
    setModalOpen(true);
  };

  return (
    <Container maxWidth="md">
      <Box
        component="main"
        sx={{
          flexGrow: 1,
          height: "100vh",
          maxHeight: "100vh",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "center",
          rowGap: 3,
        }}
      >
        <Logo />
        <ImageModal image={selectedImage} modalOpen={modalOpen} setModalOpen={setModalOpen} />
        <CarouselRender images={images} onImageClick={onImageClick} />
        <Typography
          sx={{
            textAlign: "justify",
            "& :hover": {
              color: "#16B7EA",
            },
          }}
        >
          FPL Zone is the latest companion app available to{" "}
          <Link href="https://fantasy.premierleague.com/" target="_blank">
            Fantasy Premier League
          </Link>{" "}
          (FPL) managers which enables them to make informed decisions each gameweek. The app offers
          a range of tools such as live gameweek updates, 'my team' analysis, fixture difficulty
          rating tables, and a player comparison tool. FPL Zone is a TypeScript-based React app,
          with its data being fetched from a public API provided by the Premier League. With
          well-tested functionality and eye-catching design, FPL Zone is an essential tool for any
          FPL manager looking to gain an advantage over their rivals.
        </Typography>
        <Box
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "space-evenly",
            width: "100%",
          }}
        >
          {renderLink(
            "Netlify",
            "https://fpl-zone.netlify.app/",
            <Icon sx={{ textAlign: "center" }}>
              <img
                height="100%"
                src={`${process.env.PUBLIC_URL}/assets/images/svg/netlify-icon.svg`}
                alt="netlify-icon"
              />
            </Icon>
          )}
          {renderLink(
            "GitHub",
            "https://github.com/TonyN96/fpl-zone/",
            <GitHubIcon fontSize="large" />
          )}
          {renderLink(
            "Trello",
            "https://trello.com/fplzone",
            <Icon sx={{ textAlign: "center" }}>
              <img
                height="100%"
                src={`${process.env.PUBLIC_URL}/assets/images/svg/trello-tile.svg`}
                alt="trello-icon"
              />
            </Icon>
          )}
          {renderLink(
            "Demo",
            "https://www.youtube.com/watch?v=oONwBQ1Tq7g",
            <YouTube fontSize="large" sx={{ color: "red" }} />
          )}
          {renderLink(
            "Report",
            "https://drive.google.com/file/d/10OU6mvtp5j4anB8nwsvZGNNWuiPWjvDY/view?usp=sharing",
            <Article fontSize="large" sx={{ color: "#5fdd6b" }} />
          )}
          {renderLink(
            "Presentation",
            "https://drive.google.com/file/d/11F49yAmklqhk3HHItNTcNCS1QkozgFVb/view?usp=sharing",
            <Slideshow fontSize="large" sx={{ color: "black" }} />
          )}
        </Box>
        <Typography>
          An app developed by{" "}
          <Link sx={linkStyling} target="_blank" href="https://github.com/TonyN96">
            <Typography sx={{ display: "inline", "&:hover": { color: "#16B7EA" } }}>
              Tony Naughton
            </Typography>
          </Link>
        </Typography>
      </Box>
    </Container>
  );
}
