import { Box, Modal } from "@mui/material";

interface ImageModalProps {
  image: string | null;
  modalOpen: boolean;
  setModalOpen: (value: boolean) => void;
}

export default function ImageModal({
  image,
  modalOpen,
  setModalOpen,
}: ImageModalProps): JSX.Element {
  const imageUrl = `${process.env.PUBLIC_URL}/assets/images/screenshots/${image}.png`;

  return (
    <Modal open={modalOpen} onClick={() => setModalOpen(false)}>
      <Box
        sx={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          boxShadow: 20,
          width: "95%",
        }}
      >
        <img src={imageUrl} alt="fpl-zone-screenshot" width="100%" />
      </Box>
    </Modal>
  );
}
