import React from "react";
import { Carousel } from "react-responsive-carousel";
import "react-responsive-carousel/lib/styles/carousel.min.css";

interface CarouselRenderProps {
  images: string[];
  onImageClick: (image: number) => void;
}

export default function CarouselRender({ images, onImageClick }: CarouselRenderProps): JSX.Element {
  return (
    <Carousel
      autoPlay
      showThumbs={false}
      stopOnHover
      infiniteLoop
      onClickItem={onImageClick}
      interval={4000}
    >
      {images.map((image): JSX.Element => {
        return (
          <div>
            <img
              src={`${process.env.PUBLIC_URL}/assets/images/screenshots/${image}.png`}
              alt={`${image} screenshot`}
            />
          </div>
        );
      })}
    </Carousel>
  );
}
